@use 'styles/colors';
@import 'styles/base';

:host {
  display: block;
  width: 100vw;
  max-width: 500px;
  //margin: 100px 0;

  @extend %page-card;

  .title {
    @include proxima(22px, 600, colors.$foreground_dark);
    padding-bottom: 1em;
  }

  .form-row {
    &__label {
      @include proxima(16px, 400, colors.$foreground_light-dark);
      margin-bottom: 0.2em;
      display: flex;
      align-items: center;

      a {
        margin-left: 10px;
        display: flex;
        align-items: center;

        ::ng-deep .mat-icon {
          @include mat-icon-size(20px);
          @include mat-icon-color(colors.$foreground_light-dark);
        }
      }
    }

    &__subscript {
      height: 22px;
    }

    .mat-mdc-card {
      box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.1);
      padding: 20px;
    }

    ::ng-deep {
      --mdc-outlined-text-field-disabled-input-text-color: var(--mdc-outlined-text-field-input-text-color);
    }
  }

  .currency-label {
    @include proxima(16px, 600, colors.$foreground_dark);
    padding: 0 15px;
    opacity: 0.6;
  }

  @media screen and (max-width: 630px) {
    max-width: 400px;
  }

  @media screen and (max-width: 530px) {
    max-width: 280px;
  }
}
